import styled from "styled-components";

export const Form = styled.form`
    padding: 2em;
    width: 100%;
    h3 {
        text-align: left;
        margin-bottom: .3em;
    }
    button {
        padding: .7em 1em;
    }
    @media (max-width: 768px) {
        padding: 2em 1em;
    }
`;